.signup-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #F2FBF7;
}

.signup-container {
    background-color: #FEFFFE;
    border-radius: 10px;
    padding: 40px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    min-width: 30%;
    min-height: 50%;
}

.signup-heading {
    text-align: center;
    color: #15995C;
    font-size: 32px;
    margin-bottom: 20px;
}

.signup-form {
    display: flex;
    flex-direction: column;
}

.signup-input {
    padding: 12px;
    margin-bottom: 12px;
    border-radius: 4px;
    border: 1px solid #CCCCCC;
}

.signup-button {
    background-color: #15995C;
    color: #FEFFFE;
    padding: 12px 24px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.signup-button:hover {
    background-color: #12844D;
}

.signup-button:focus {
    outline: none;
}