.navbar {
    background-color: #FEFFFE;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

.logo img {
    /* width: 100px; */
    /* Adjust as needed */
    height: 4rem;
    /* Adjust as needed */
}

.navbar-links {
    display: flex;
    align-items: center;
}

.navbar-links-left {
    display: flex;
    align-items: center;
}

.navbar-links-left a {
    margin-right: 10px;
    text-decoration: none;
    color: #000000;
}

.signin-btn {
    text-decoration: none;
    background-color: #15995C;
    border-radius: 4px;
    color: #ffffff;
    padding: 8px 16px;
    border: none;
    cursor: pointer;
}

.menu-toggle {
    background: none;
    border: none;
    cursor: pointer;
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    width: 30px;
    height: 20px;
}

.menu-toggle .bar {
    width: 100%;
    height: 3px;
    background-color: #000000;
    transition: transform 0.3s ease-in-out;
}

.menu-toggle.open .bar:nth-child(1) {
    transform: translateY(8px) rotate(-45deg);
}

.menu-toggle.open .bar:nth-child(2) {
    opacity: 0;
}

.menu-toggle.open .bar:nth-child(3) {
    transform: translateY(-8px) rotate(45deg);
}

/* Content Section styles */
.content {
    background-color: #F2FBF7;
    min-height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content-center {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 0 auto;
    flex-wrap: wrap;
}

.content-left {
    flex-direction: column;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 49em;
}

.content-left h1 {
    color: #212123;
    margin: 5px;
    font-size: 4rem;
    font-weight: 600;
    /* Adjust as needed */
}

.content-left p {
    font-size: larger;
    font-weight: 300;
    margin: 2vh 0.5rem;
    /* Adjust as needed */
}

.content-left h2 {
    font-size: 24px;
    /* Adjust as needed */
}

.content-right {
    margin-left: 20px;
}

.content-right img {
    width: 22rem;
    height: auto;
    border-radius: 27px;
    margin-top: 20px;
    margin: 2rem 5rem;
}

/* second div */
.second-div {
    background-color: #FEFFFE;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 60vh;
    padding: 20px;
}

.child-div {
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
    margin: 1rem 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    border-radius: 4px;
    padding: 20px;
    opacity: 0;
    /* Initially hide the div */
    transform: translateX(-100px);
    /* Initially position the div off-screen to the left */
    animation: slideIn 0.5s ease-in-out forwards;
}

.child-div img {
    width: 25vw;
    opacity: 0;
    /* Initially hide the image */
    animation: slideIn 0.5s ease-in-out forwards;
    /* opacity: 0; */
}

.child-div.loaded {
    opacity: 1;
    /* Show the div and image when loaded */
}

.paragraph-div p {
    font-size: larger;
    font-weight: 300;

    /* Adjust as needed */
}

.top-center-btn {
    margin: 0 1rem;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.125);
    background-color: #ffffff;
    color: #000000;
    border: none;
    cursor: pointer;
    font-size: 14px;
    font-weight: bold;
    width: 6rem;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0.5rem;
}

/* Third Div styles */
.third-div {
    background-color: #F2FBF7;
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 59vh;
    padding: 20px;
}


.third-div-left,
.third-div-right {
    flex-basis: 50%;
    padding: 20px;
    opacity: 0;
    transform: translateX(-100px);
    animation: slideIn 0.5s ease-in-out forwards;
}

.third-div-left {
    text-align: center;
}

.third-div-right {
    display: flex;
    justify-content: center;
    align-items: center;
}

.third-div-left h2 {
    color: #212123;
    margin: 5px;
    font-size: 32px;
    font-weight: 600;
}

.third-div-left p {
    font-size: 16px;
    font-weight: 300;
}

.third-div-right img {
    width: 200px;
    height: auto;
    border-radius: 8px;
    /* height: 3rem; */
}

.initial-content {
    display: flex;

}

.initial-content h2 {
    font-size: 6rem;

}

.initial-content img {
    width: 6rem;
    margin: 0px 1rem;
    translate: 0px -14px;
}

.paragraph {
    max-width: 40vw;
    font-weight: 300;
    margin: 2rem 0px;
    font-size: large;
}

.profile-pic {
    height: 21vw;
    border-radius: 1rem;
}

.floating {
    /* position: absolute; */
    animation: floating 6s ease-in-out infinite;
}

#contact {
    background-color: #FEFFFE;
    padding: 60px 0;
}

.container {
    max-width: 800px;
    margin: 0 auto;
}

.h2 {
    text-align: center;
    margin-bottom: 30px;
}

.span_about_contact {
    display: block;
    text-align: center;
    color: #666;
    margin-bottom: 40px;
}

.form_container {
    background-color: #FFF;
    padding: 40px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.contact__form-field {
    margin-bottom: 20px;
}

.contact__form-label {
    display: block;
    font-weight: bold;
    margin-bottom: 10px;
}

.contact__form-input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.contact__form-input::placeholder {
    color: #999;
}

.contact__form-input:focus {
    outline: none;
    border-color: #666;
}

.contact__form-input:focus::placeholder {
    color: #666;
}

.contact__form-input[type="submit"] {
    background-color: #FEFFFE;
    color: #333;
    padding: 10px 20px;
    border: 1px solid #333;
    border-radius: 4px;
    cursor: pointer;
}

.contact__form-input[type="submit"]:hover {
    background-color: #333;
    color: #FEFFFE;
}

.contact {
    font-size: 3rem;
    text-align: center;
}

.span_about_contact {
    font-weight: 300;
    font-size: large;
}


.container2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
}


@keyframes floating {
    0% {
        transform: translateY(0%);
    }

    50% {
        transform: translateY(-5%);
    }

    100% {
        transform: translateY(0%);
    }
}

@keyframes slideIn {
    0% {
        opacity: 0;
        transform: translateX(-100px);
        /* Initial position off-screen to the left */
    }

    100% {
        opacity: 1;
        transform: translateX(0);
        /* Final position on-screen */
    }
}


/* Responsive Styles */
@media screen and (max-width: 768px) {
    .navbar-links {
        display: none;
    }

    .content-center {
        flex-direction: column;
    }

    .content-right {
        margin: auto;
    }

    .content-right img {
        margin: 3rem;
        border-radius: 2rem;
        width: 66vw;
    }

    .navbar-links.open {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .menu-toggle .bar {
        background-color: #000000;
        margin: 4px 0;
        transition: transform 0.3s ease-in-out;
    }

    .menu-toggle.open .bar:nth-child(1) {
        transform: translateY(8px) rotate(-45deg);
    }

    .menu-toggle.open .bar:nth-child(2) {
        opacity: 0;
    }

    .menu-toggle.open .bar:nth-child(3) {
        transform: translateY(-8px) rotate(45deg);
    }

    .signin-btn {

        margin-top: 10px;
    }

    /* second div */
    .second-div {
        flex-direction: column;
        align-items: center;
    }

    .child-div {
        width: 95%;
        transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
    }



    .child-div:last-child {
        margin-bottom: 0;
    }

    .child-div img {
        width: 100%;
        height: auto;
        margin-bottom: 10px;
    }

    .third-div {
        flex-direction: column;
    }

    .third-div-left,
    .third-div-right {
        flex-basis: auto;
        width: 100%;
        margin-bottom: 20px;
    }

    .third-div-right img {
        width: 100%;
        max-width: 300px;
    }

    .paragraph {
        max-width: 100%;
    }

    .profile-pic {
        height: 36vh;
    }

    .inner-div {
        max-width: 100%;
    }
}



/* Animation */
@keyframes slideIn {
    0% {
        opacity: 0;
        transform: translateX(-100px);
    }

    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

/* footer */
.footer {
    background-color: #F2FBF7;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}

.center {
    text-align: center;
}

.footer-left {
    min-width: 70vw;
}

.footer-left h2 {
    font-size: 28px;
    color: #333;
    margin-bottom: 20px;
}

.footer-description {
    font-size: 16px;
    color: #555;
    line-height: 1.5;
}

.footer-right {
    width: 15vw;
    display: inherit;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.footer-right h2 {
    font-size: 28px;
    color: #333;
    margin-bottom: 20px;
}

.social-icons {
    display: flex;
    gap: 20px;
}

.social-icons a {
    color: #555;
    font-size: 24px;
    transition: color 0.3s ease;
}

.social-icons a:hover {
    color: #007bff;
}

.copywrite {
    background-color: #F2FBF7;
    padding: 20px;
    text-align: center;
    color: #555;
    font-size: 14px;
}