.folder-container-transfer {
    background-color: #FEFFFE;
    border-radius: 10px;
    padding: 40px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    min-width: 43%;
    min-height: 67%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.folder-container-transfer>*:not(:last-child) {
    display: block;
    margin-bottom: 30px;
}

.font-light {
    font-weight: 300;
    font-size: large;
}

.icon-text {
    display: flex !important;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.drive-home-transfer {

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #F2FBF7;
}

.file-icon-img {
    width: 4rem;
}

.submit-button {
    background-color: #15995C;
    color: #FEFFFE;
    padding: 12px 24px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    width: 30rem;
}