.drive-home {
    padding: 20px;
    min-height: 100vh;
}

.drive-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}

.drive-heading {
    font-size: 24px;
    color: #000000;
}

.create-folder-btn {
    background-color: #FFFFFF;
    color: #000000;
    border: none;
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    font-weight: bold;
}

.create-folder-btn:hover {
    background-color: #E5E5E5;
}

.create-folder-form {
    justify-content: center;
    align-items: center;
    margin: 1rem;
    display: flex;

}

.create-folder-input {
    padding: 8px;
    border: 1px solid #CCCCCC;
    border-radius: 4px;
    margin-right: 10px;
    width: 200px;
}

.create-folder-submit-btn {
    background-color: #15995C;
    color: #FFFFFF;
    border: none;
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
}

.create-folder-submit-btn:hover {
    background-color: #12844D;
}

.folder-container {
    background-color: #F3FBF6;
    min-height: 70vh;
    border-radius: 1rem;
    margin: auto;
    max-width: 86vw;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(18rem, 1fr));
    grid-gap: 20px;
    padding: 20px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

.folder-card {
    min-height: 10rem;
    max-height: 13rem;
    max-width: 19rem;
    position: relative;
    background-color: #FFFFFF;
    padding: 16px;
    border-radius: 1.25rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    ;
    transition: background-color 0.3s ease;

}

.folder-card:hover {
    background-color: #F2F2F2;
}

.folder-icon {
    width: 64px;
    height: 64px;
}

.folder-name {
    margin-top: 10px;
    font-size: 14px;
    color: #000000;
}

.trash {
    text-decoration: none;
    align-items: center;
    justify-content: center;
    height: 2rem;
    position: absolute;
    top: 1rem;
    display: flex;
    right: 1rem;
    width: 2rem;
    background-color: rgb(229 229 229);
    border-radius: 50%;
}

