}

.file-upload {
    display: flex;
    align-items: center;
    margin-top: 20px;
    /* Adjust as needed */
}

.file-upload-input {
    display: none;
}

.file-upload-label {
    background-color: #4c51bf;
    color: #ffffff;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.file-upload-label:hover {
    background-color: #6670e0;
}

.file-upload-label span {
    margin-left: 5px;
}

.uploaded-file-container {
    display: flex;
    align-items: center;
    margin-top: 10px;
    /* Adjust as needed */
}

.uploaded-file-icon {
    margin-right: 10px;
    /* Adjust as needed */
}

.uploaded-file-name {
    font-size: 16px;
}

.file-assets {
    display: flex;
    width: 100%;
    justify-content: space-around;
    font-size: 1.25rem;
}

.file-assets i {
    background-color: rgb(229 229 229);
    border-radius: 50%;
    width: 2.25rem;
    height: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.top-btn {
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
    margin: 1rem 1rem;
}

.top-btn-mydrive {
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
    margin: 1rem 1rem;
    min-width: 8rem;
    max-width: 10rem;
    height: 4rem;
    text-align: center;
    border-radius: 1rem;
    cursor: pointer;
    background-color: #e6ffef;
}

.top-btn-mydrive p {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.top-btn-div {
    display: flex;
}

.cancel-i {
    border-radius: 10px;
    margin: 0 1rem;
    display: flex;
    height: 2rem;
    text-align: center;
    min-width: 2rem;
    cursor: pointer;
    background-color: #ff5959;
    justify-content: center;
    align-items: center;
}