.signin-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #F2FBF7;
}

.signin-container {
  background-color: #FEFFFE;
  border-radius: 10px;
  padding: 40px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  min-width: 30%;
  min-height: 50%;
}

.signin-heading {
  text-align: center;
  color: #15995C;
  font-size: 32px;
  margin-bottom: 4rem;
}

.signin-form {
  display: flex;
  flex-direction: column;
}

.signin-input {
  padding: 12px;
  margin-bottom: 12px;
  border-radius: 4px;
  border: 1px solid #CCCCCC;
}

.signin-button {
  background-color: #15995C;
  color: #FEFFFE;
  padding: 12px 24px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: 100%;
}

.signin-button:hover {
  background-color: #12844D;
}

.signin-button:focus {
  outline: none;
}

.signup-para {
  margin: 1rem 0;
}